.mainContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #ffff;
  border-radius: 20px;
  width: 600px;
  padding: 20px;
}
.projectTitle {
  background-color: mediumspringgreen;
  padding: 20px 200px;
  border-radius: 5px;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.4);
}
.contactTitle {
  background-color: mediumspringgreen;
  padding: 10px 200px;
  border-radius: 5px;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.4);
}
